.ImageGallery {
  display: grid;
  max-width: calc(100vw - 125px);
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.movies__item {
  display: flex;
  justify-content: center;
}

.movie__card {
  border-radius: 2px;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  background-color: rgb(67, 90, 76);
  height: 450px;
  width: 240px;
  padding: 5px;
  box-shadow:
    0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14),
    0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.movie__img {
  display: block;
  max-width: 100%;
  height: auto;

  background: url(../../images/dummy.jpg);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;

  border-radius: 2px;
  overflow: hidden;
}

.movie__card:hover {
  z-index: 100;
  cursor: pointer;
  transform: translateY(-5px);
  box-shadow:
    0px 0px 20px 10px #75db9c,
    0px 0px 5px 5px rgb(255, 255, 255),
    0px 2px 2px rgba(0, 0, 0, 0.12);
  transition-property: all;
  transition-duration: 500ms;
  transition-timing-function: ease-out;
  transition-delay: 100ms;
  animation-direction: alternate;
  -webkit-animation-direction: alternate;
}

.movie__name {
  color: rgb(255, 249, 249);
  font-weight: 500;
  font-size: 12px;
  line-height: 1.6;
  text-transform: uppercase;
}

.movie__genre,
.movie__year {
  color: rgb(194, 201, 152);
  font-weight: 700;
  font-size: 10px;
  line-height: 1.3;
}

.movie__label {
  padding: 10px;
}
